:root{
    --handle-size: 3.2rem;
    --img-gap: .25rem
}

.container{
    display: flex;
    justify-content: center;
    overflow: hidden;
}

body {
    margin: 0px;
    background-color: #333;
    color: white;
  }

.slider{
    --items-per-screen: 4;
    --slider-index: 0;
    display: flex;
    flex-grow: 1;
    margin: 0 var(--img-gap);
    transform: translateX(calc(var(--slider-index) * -100%));
    transition: transform 250ms ease-in-out;
}

.movie_poster{
    flex: 0 0 calc(100% / var(--items-per-screen));
    max-width: calc(100% / var(--items-per-screen));
    aspect-ratio: 16 / 9;
    padding: var(--img-gap);
    border-radius: 1rem;
    overflow-y: hidden;
    overflow-x: scroll;
    cursor: pointer;
    transition: transform 450ms;
}

.movie_poster::-webkit-scrollbar{
    display: none;
}

.movie_poster:hover{
    transform: scale(1.1);
    opacity: 1;
}

.handle {
    border: none;
    border-radius: 1rem;
    width: var(--handle-size);
    background-color: rgba(0, 0, 0, 0.25);
    flex-grow: 0;
    flex-shrink: 0;
    z-index: 10;
    margin: var(--img-gap) 0;
    cursor: pointer;
    font-size: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    line-height: 0;
    transition: background-color 150ms ease-in-out;
}

.left-handle{
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}

.right-handle{
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.handle:hover,
.handle:focus {
    background-color: rgba(0, 0, 0, 0.5);
}

.text{
    transition: transform 150ms ease-in-out;
}

.handle:hover .text,
.handle:focus .text {
    transform: scale(1.2);
}

.header{
    color: white;
    display: flex;
    justify-content: space-between;
    margin-top: 3%;
    padding: .5rem calc(var(--img-gap) * 2 + var(--handle-size));
    align-items: center;
}

.title{
    font-size: 2rem;
    margin: 0;
}

.progress-bar{
    display: flex;
    gap: .25rem;
}

.progress-item{
    flex: 0 0 1.5rem;
    min-width: 1.5rem;
    height: .5rem;
    background: rgba(255, 255, 255, .5);
}

.progress-item.active{
    background: rgba(255, 255, 255, .9);
}

@media (max-width: 1000px) {
    .slider {
      --items-per-screen: 3;
    }
  }
  
  @media (max-width: 500px) {
    .slider {
      --items-per-screen: 2;
    }
  }